*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

input[type=number] {
  -moz-appearance: textfield;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button,
input {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  display: flex;
  object-fit: cover;
}

* {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  overflow: hidden;
}

.container {
  padding: 0 0.9375rem;
  margin: 0 auto;
}